.card-leaderboard {
  display: block;
  width: 90vw;
  margin: 0 auto 10px;
  border-radius: 0 0 10px 10px;
  background-color: #fff;
  background: #fff;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}

.card-leaderboard-labels {
  width: 90vw;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-leaderboard-labels h3 {
  margin: 10px 0 0;
  padding: 0;
  font-size: 1rem;
  font-weight: 400;
  color: #888888;
}

.card-leaderboard-users, .card-stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-leaderboard-users:hover {
  background: rgba(231, 229, 229, 0.46);
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
}

.card-leaderboard-users h3, .card-stats h3{
  font-weight: 300;
  color: #282E31;
}

.card-leaderboard-users h3, .card-leaderboard-labels h3, .card-stats h3 {
  font-weight: 300;
  font-size: 24px;
  text-align: left;
  flex: 2;
}

.card-leaderboard-labels h3 {
  font-weight: 800;
  font-size: 14px;
}


.card-leaderboard-users h3:first-child, .card-leaderboard-labels h3:first-child, .card-stats h3:first-child {
  margin-left: 20px;
  text-align: left;
  flex: 3;
}

.card-leaderboard-labels h3:first-child {
  margin-left: 0;
  margin-right: 20px;
}

.card-stats h3:first-child {
  color: #07E0E0;
}



