body {
  background: #f3f3f3;
}

h2 {
  color: #A15BFA;
  margin: 0;
  padding: 0 0 10px;
}

.game-and-score {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  width: 90vw;
  max-width: 800px;
  margin: 20px auto;
  padding: 0;
  text-align: left;
  font-size: 1.5rem;
}

.game-and-score h3, .game-and-score h5 {
  font-weight: 500;
  margin: 0;
  padding: 5px 0;
}

.game-and-score h5 {
  color: #A15BFA;
}

.progress {
  width: 90vw;
  max-width: 800px;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 30px;
}

.bar {
  width: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.bar h5, .time h5 {
  margin: 0;
  padding: 0;
  line-height: 30px;
  font-size: 1rem;
  font-weight: 500;
  color: #A15BFA;
}

.quiz-area {
  height: 470px;
  width: 90vw;
  max-width: 800px;
  margin: 20px auto;
  background: #FFFFFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.question {
  margin: 0;
  padding: 10px 0 0;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  border-radius: 10px;
  color: #282E31;
}

.question:hover {
  border: none;
}

.question h3 {
  margin: 0;
  padding: 0 0 5px;
}

.question h6 {
  margin: 0;
  padding: 0;
  font-weight: 100;
  font-size: 1rem;
}

.questions {
  margin: 100px auto 0;
}

.questions button {
  border-radius: 10px;
  padding: 15px 40px;
  background: #07E0E0;
  color: #fff;
  font-size: 1.4rem;
}

.answers {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.answer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 20vh;
  width: 30vw;
  max-width: 300px;
  margin: 20px 25px 20px;
  padding: 0;
  background: #FFFFFF;
  mix-blend-mode: normal;
  box-shadow: 0px 32px 40px rgba(210, 210, 210, 0.54);
  border-radius: 10px;
}

.answer:hover {
  box-shadow: 0px 32px 80px rgba(150,150,150, 0.55);
  border: none;
}

.answer h4 {
  text-align: center;
  height: 30%;
  width: 100%;
  margin: 0;
  padding: 10px 0 0;
  font-size: 1.8rem;
  font-weight: 900;
  color: #000;
  border-radius: 10px 10px 0 0;
}

.answer h3 {
  margin: 0;
  width: 100%;
  height: 40%;
  font-size: 1.3rem;
  font-weight: 500;
  color: #282E31;
}

.correct, .correct:hover {
  border: 1px solid #1DC44B;
  box-shadow: 0px 32px 40px rgba(29, 195, 75, 0.12);
}

.correct h4 {
  color: #1DC44B;
  background: 0;
}

.wrong, .wrong:hover {
  border: 1px solid #f00;
}

.wrong h4 {
  color: #f00;
  background: 0;
}

.times-up-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.33);
}

.times-up-modal {
  position: fixed;
  top: 50vh;
  left: 50vw;
  height: 300px;
  width: 400px;
  transform: translate(-50%, -40%);
  background: #FFFFFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.times-up-modal-content {
  margin: 30px 0 0;
}

.times-up-modal span {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

.times-up-modal h2 {
  margin: 10px 0;
  padding: 0;
  font-size: 3rem;
  color: #f00;
}

.times-up-modal h4 {
  text-align: center;
  margin: 30px 0 0;
  padding: 0;
  font-size: 1rem;
  color: #282E31;
}

.times-up-modal h3 {
  margin: 0;
  padding: 0;
  font-size: 4rem;
  font-weight: 300;
  color: #A15BFA;
}
