html,
body {
    width: 100%;
    height: 100%;
    text-align: center;
}

$field-border: #07E0E0;
//Root Container
.root-container-login, .root-container-register {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.root-container-login {
  background: #282E31;
}

.root-container-register {
  background: #eee;
}


.box-container-login {
    margin:  25px 0 0;
    display: flex;
    flex-direction: column;
    max-width: 24em;
    min-width: 20em;
    box-shadow: 20px 20px 20px 20px rgba(15, 15, 15, 0.2);
    border-radius: 10px;
    padding: 24px;
    background: #fff;
}

.box-container-register {
    display: flex;
    flex-direction: column;
    max-width: 50em;
    min-width: 40em;
    box-shadow: 20px 20px 20px 20px rgba(15, 15, 15, 0.2);
    border-radius: 6px;
    padding: 24px;
    background: #fff;
}

.inner-container-login {
    width: 85%;
    margin: 0 0 0 15%;
    transition: visibility 0.2s ease-out;
    text-align: left;
}

.inner-container-register {
    width: 90%;
    margin: 0 0 0 10%;
    transition: visibility 0.2s ease-out;
    text-align: left;
}

.inner-container-register.show, .inner-container-login.show {
    visibility: visible;
}

//Header
.login-header, .register-header {
    background: inherit;
    margin: 0 0 20px;
}

.login-header h1, .register-header h1 {
    padding: 0;
    margin: 0;
    font-family: sans-serif;
    font-size: 1.5em;
    font-weight: 800;
}

.box {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.input-group {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

.input-group-double-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 10px;
}

.input-group-double {
  display: flex;
  flex-direction: column;
  margin: 0;
  width: 50%;
}

//Label
label {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 800;
}

//Input
.login-input {
    border: 0;
    padding: 0;
    margin: 10px 0;
    width: 80%;
    font-family: sans-serif;
    font-size: 1.2em;
    font-weight: 300;
    color: #282E31;
    background: inherit;
}

//Input Placeholder
.login-input::placeholder {
    font-family: sans-serif;
    font-size: 16px; //color: rgba(46, 213, 116, 0.839);
    color: rgba(15, 15, 15, 0.4);
    margin: 0;
    padding: 0;
    width: 100%;
}

.login-input:focus {
  outline: none;
  border-bottom: 2px solid $field-border;
}

.login-extras {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.login-btn, .register-btn {
  display: flex;
  justify-content: center;
  margin: 0 0 10px;
  padding: 0.5rem 3rem;
  /* background-color: #417cef; */
  border: 0;
  font-size: 18px;
  border-radius: 10px;
  text-decoration: none;
  font-family: sans-serif;
  background-color: #07E0E0;
  color: #fff;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}

.register-btn {
  padding: 0.5rem 0.5rem;
}

.login-btn:hover, .register-btn:hover {
    transform: scale(0.97);
    outline: none;
}

.login-btn:focus, .register-btn:focus {
  outline: none;
}

.login-btn:active, .register-btn:active {
    transform: scale(0.94);
}

.danger-error {
    color: #e74c3c;
    font-size: 16px;
}

//Password
.password-state {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
}

.pwd {
    height: 6px;
    flex: 1;
    visibility: hidden;
}

.pwd-weak {
    background-color: #e74c3c;
}

.pwd-medium {
    background-color: #3498db;
}

.pwd-strong {
    background-color: #2ecc71;
}

.show {
    visibility: visible;
}

.login-register {
  margin: 10px 0 0;
  padding: 0;
  background: inherit;
  font-size: 0.7rem;
  font-weight: 500;
  text-align: left;
}

.form-img {
  margin: 0 0 20px 0;
}
