.App {
  text-align: center;
}

.home-content {
  margin: 0;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    background-color: inherit;
    text-align: center;
}

/******* Nav Styling *******/
.nav {
  display: block;
  height: 30px;
  margin: 10px 0 0 5%;
  padding: 0;
  list-style: none;
  justify-content: space-around;
}

.links {
  display: inline;
  float: left;
  font-family: sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #999;
  text-decoration: none;
  margin: 0 40px 0 0;
  padding: 0;
}

h3.links {
  color: #07E0E0;
  font-weight: 500;
font-size: 18px;
  text-decoration: underline;
}



/******* Universals Styling *******/

/*  Universal  */

.header {
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background-color: #282E31;
}

/*  Universal  */
.header img {
  margin-left: 5%;
}

.header img:nth-of-type(2) {
  margin: 0 25px 0 auto;

}

.header h3 {
  color: #fff;
  text-decoration: none;
  margin: 0 0 0 50px;
}

/*  Universal  */
.info {
  width: 90%;
  margin: 10px auto;
  display: block;
  text-align: left;
}

/*  Universal  */
.info h3 {
  display: inline;
  margin: 0;
  margin-left: 0;
}

.title h3 {
  font-weight: 300;
  font-size: 24px;
}

/*  Universal  */
.back {
  color: #07E0E0;
  text-decoration: none;
}

.back h3 {
  font-weight: 400;
  font-size: 20px;
}

/*  Universal  */
.title {
  font-weight: 300;
  font-size: 24px;
  margin-bottom: 0;
}

/*  Universal  */
.add-container h3 {
  display: inline;
  font-weight: 500;
  font-size: 48px;
}

/*  Universal  */
.add-container form {
  margin-left: 50px;
  display: inline;
}

/*  Universal  */
.add-container form .add-input {
  background: 0;
  margin: 0;
  padding: 0;
  display: inline;
  vertical-align: center;
  font-size: 1rem;
  font-weight: 200;
  color: #07E0E0;
}

.add-container-client {
  margin-top: 40px;
}

/*  Universal  */
.storage-container {
  width: 100%;
  margin: 25px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

/*  Universal  */
.labels {
  margin: 0;
  padding: 0;
  display: block;
  width: 90vw;
}

/*  Universal  */
.labels h5 {
  padding: 0;
  margin: 0;
  display: inline;
  color: #333;
  font-weight: 100;
  font-size: 1.2em;
}

/*  Universal  */
.labels h5:first-child {
  margin-left: 50px;
  float: left;
}

/*  Universal  */
.card, .card-open {
  display: block;
  height: 100px;
  width: 90vw;
  margin: 10px auto;
  border-radius: 10px;
  background-color: #fff;
  color: #07E0E0;
  font-size: 1.2em;
  letter-spacing: 2px;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}

/*  Universal  */
  .card:hover {
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
                0 6.7px 5.3px rgba(0, 0, 0, 0.048);
  }

/*  Universal  */
  .card .content, .card-stats {
    height: 100px;
    width: 90vw;
    display: table-cell;
    vertical-align: middle;
  }

  .card-stats h3 {
    text-decoration: 0;
  }

/*  Universal  */
  .card-name {
    color: #07E0E0;
    line-height: 3.5em;
    float: left;
    margin-left: 50px;
  }

  .card-open {
    margin-bottom: 0;
    border-radius: 10px 10px 0 0;
    box-shadow: 0;
    border-bottom: 1px solid #000;
  }

/*  Universal  */
  button,
  .add-input[type=submit] {
    outline: none;
    border: none;
    transition: background-color 0.2s ease,
                color 0.2s ease;
  }

  button,
  .add-input[type=submit]:hover {
    color: #07C0D0;
    cursor: pointer;
  }


/* modals */
.modal-label {
  margin: 30px 0 0 70px;
  padding: 0;
  font-size: 1rem;
  color: #282E31;
}

.add-game-modal-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.33);
}

.add-game-modal {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 300px;
  width: 400px;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -70%);
  background: #F3F3F3;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.add-game-modal button:first-child {
  margin: 20px 20px 0 0;
  padding: 0;
  align-self: flex-end;
  color: #282E31;
}

.add-game-modal h3 {
  margin: 0 0 0 70px;
  padding: 0;
  font-weight: 800;
  color: #282E31
}

.add-game-modal form {
  margin: 20px 0 0 70px;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.add-game-modal label {
  margin: 0;
  padding: 0;
}

.add-game-modal input {
  margin: 20px 0 0;
  padding: 0;
  width: 200px;
  background: inherit;
  border: 0;
}

.add-game-modal input:hover {
  border-bottom: 2px solid #07E0E0;
}

.add-game-modal input:hover, .add-game-modal input:active, .add-game-modal input:focus {
  outline: 0;
}

.add-game-modal button:last-child {
  margin: 50px 0 0 70px;
  padding: 10px 25px;
  background: #07E0E0;
  color: #fff;
  border-radius: 10px;
}

.add-user-modal-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.33);
}

.add-user-modal {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 500px;
  width: 400px;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  background: #F3F3F3;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.hr {
  margin: 50px auto 20px;
  width: 65%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.hr span {
  text-align: center;
  flex: 2;
  color: #919191;
}

.hr div {
  flex: 3;
  height: 1px;
  border-bottom: 1px solid #A8A8A8;
}

.add-user-modal button:first-child {
  margin: 20px 20px 0 0;
  padding: 0;
  align-self: flex-end;
  color: #282E31;
}

.add-user-modal h3 {
  margin: 0 0 0 70px;
  padding: 0;
  font-weight: 800;
  color: #282E31
}

.add-user-modal form {
  margin: 20px 0 0 70px;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.add-user-modal label {
  margin: 0;
  padding: 0;
}

.add-user-modal input {
  margin: 20px 0 0;
  padding: 0;
  width: 200px;
  background: inherit;
  border: 0;
}

.add-user-modal input:hover {
  border-bottom: 2px solid #07E0E0;
}

.add-user-modal input:hover, .add-user-modal input:active, .add-user-modal input:focus {
  outline: 0;
}

.add-user-modal button:last-child {
  margin: 50px 0 0 70px;
  padding: 10px 25px;
  background: #07E0E0;
  color: #fff;
  border-radius: 10px;
}

.add-client-modal-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.33);
}

.add-client-modal {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 600px;
  width: 800px;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  background: #F3F3F3;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.add-client-modal button:first-child {
  margin: 20px 20px 0 0;
  padding: 0;
  align-self: flex-end;
  color: #282E31;
}

.add-client-modal h3 {
  margin: 0 0 0 70px;
  padding: 0;
  font-weight: 800;
  color: #282E31;
}

.add-client-modal .form-container-client {
  margin: 20px auto 0;
  width: 660px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.add-client-modal form {
  margin: 0;
  padding: 0;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.add-client-modal label {
  margin: 0;
  padding: 0;
}

.add-client-modal input {
  margin: 20px 0 0;
  padding: 0;
  width: 90%;
  background: inherit;
  border: 0;
}

.add-client-modal input:hover {
  border-bottom: 2px solid #07E0E0;
}

.add-client-modal input:hover, .add-client-modal input:active, .add-client-modal input:focus {
  outline: 0;
}

.add-client-modal button:last-child {
  margin: 30px 0 0 70px;
  padding: 10px 25px;
  background: #07E0E0;
  color: #fff;
  border-radius: 10px;
}

/**** Create User ****/
.user-radio-container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  margin: 0;
  padding: 0;
  text-align: left;
}

.user-radio-container .user-permissions {
  margin: 0;
}

.user-radio-container input[type="radio"] {
  display: inline;
  margin: 0;
  padding: 0;
  width: 4%;
}

.user-radio-container h4 {
  font-size: 1rem;
  color: #282E31;
}

.user-radio {
  margin: 0;
  padding: 0;
  height: 10px;
  width: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
  border: 1px solid #000;
}

.user-radio-container:nth-child(4) .user-radio, .user-radio-container:nth-child(5) .user-radio, .user-radio-container:nth-child(6) .user-radio, .user-radio-container:nth-child(7) .user-radio {
  margin: 0 0 0 25px;
}

.default {
  margin: 0;
  padding: 0;
  width: 8px;
  height: 8px;
  border-radius: 4px;
}

.selected {
  background: #07E0E0;
}

select:focus, select:active {
  outline: 0;
}

.contact-container {
  height: 130px;
  width: 90vw;
  margin: 0 auto 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contact-heading {
  display: flex;
  justify-content: flex-start;
}

.contact-heading h3 {
  margin: 0;
  padding: 0;
  font-weight: 300;
  color: #282E31;
}

.contact-card {
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border-radius: 10px;
}

.contact-content, .contact-content-street {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: baseline;
}

.contact-content {
  flex: 1;
}

.contact-content-street {
  flex: 2;
}

.contact-content h4, .contact-content-street h4 {
  margin: 0 0 0 25px;
  padding: 0;
  font-size: 1rem;
  font-weight: 700;
  color: #07E0E0;
}

.contact-content h5, .contact-content-street h5, .contact-content input[type="text"], .contact-content-street input[type="text"] {
  margin: 0 0 0 25px;
  padding: 0;
  font-size: 1rem;
  font-weight: 300;
  color: #282E31;
}

.contact-content input[type="text"], .contact-content-street input[type="text"] {
  outline: 0;
  border: 0;
}

.contact-content input[type="text"]:hover, .contact-content-street input[type="text"]:hover {
  border-bottom: 2px solid #07E0E0;
}

.info-save {
  margin: 0 20px;
  padding: 5px 10px;
  background: #07E0E0;
  border-radius: 10px;
  color: #fff;
}

.info-cancel {
  padding: 5px 10px;
  border: 1px solid #07E0E0;
  border-radius: 10px;
  color: #07E0E0;
}

/* error notice */

.error-notice {
  margin: 1rem 0;
  padding: 0.5rem;
  border: 1px solid #e07c7c;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8d6d6;
}
.error-notice button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #df4343;
  color: #ffffff;
}

.colors, .logo {
  background: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  height: 550px;
  width: 40vw;
}

.colors h5 {
  margin: 0;
  padding: 0;
}

.theme-edit {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.colorsDisplay {
  background: #fff;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
  height: 350px;
  width: 40vw;
  border-radius: 10px;
}

.logoDisplay {
  background: #fff;
  height: 350px;
  width: 40vw;
  border-radius: 10px;
}

.theme-edit-button {
  margin: 20px 0 0 0;
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.colorsDisplay button img, .logoDisplay button img {
  margin-right: 20px;
  height: 20px;
}

.colorsDisplay button, .logoDisplay button {
  background: inherit;
  float: right;
}

.colorsDisplay h3, .logoDisplay h3 {
  text-align: left;
  margin: 0 0 10px 50px;
  font-size: 2rem;
}

.logoDisplay h3 {
  margin: 0 0 20px 50px;
}

.logoDisplay img {
  margin: 0 0 0 50px;
  float: left;
}

.display-section {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 80%;
}

.display-section h5 {
  margin-left: 25px;
  padding: 0;
}

.circleDisplay {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin: 0 0 0 50px;
}
