.result-rating {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  width: 90vw;
  max-width: 800px;
  margin: 0 auto;
  padding: 0;
  text-align: left;
  font-size: 1.5rem;
  color: #282E31;
}

.result-label {
  width: 90vw;
  max-width: 800px;
  margin: 30px auto 10px;
  padding: 0;
  font-size: 1.4rem;
}

.rating-container {
  padding: 0 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
}

.rating-container h5 {
  margin: 0 0 5px;
  padding: 0;
}

.stats, .leaderboard {
  width: 90vw;
  max-width: 800px;
  margin: 0 auto 50px;
  padding: 25px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
}

.stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.stat {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stat h3 {
  margin: 0;
  padding: 0;
  color: #888888;
}

.stat h4 {
  margin: 0;
  padding: 0;
  font-size: 5rem;
  color: #A15BFA;
}

.stat h4 span {
  margin: 0;
  padding: 0;
  font-size: 2rem;
  color: #A15BFA;
}

.leaderboard-labels {
  width: 90vw;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.leaderboard-labels h3 {
  font-size: 0.7rem;
  font-weight: 800;
  color: #888888;
}

.leaderboard-users {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.leaderboard-users:hover {
  background: rgba(231, 229, 229, 0.46);
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
}

.leaderboard-users h3 {
  font-weight: 300;
  color: #282E31;
}

.leaderboard-users h3, .leaderboard-labels h3 {
  flex: 2;
}

.leaderboard-users h3:first-child, .leaderboard-labels h3:first-child {
  margin-left: 20px;
  text-align: left;
  flex: 3;
}
