.modal {
    position: block;
    top: 0;
    left: 0;
    z-index: 500;
    width: 100%;
    height: 100%;
    color: #000;
}

h4 {
  margin: 0;
  padding: 30px 30px 0;
  text-align: left;
  color: #000;
  font-family: sans-serif;
  font-size: 2em;
  font-weight: 100;
}

.questions-list {
  height: 10%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.questions-list ul {
  margin: 0;
  padding: 0;
}

.questions-list li {
  display: inline;
  list-style: none;
  margin: 10px;
  padding: 0;
}

.edit-section {
    background-color: #fff;
    margin: 50px auto 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 90%;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);;
    border-radius: 10px;
    padding: 24px;
}

.west {
  width: 60%;
}

.east {
  align-self: baseline;
  width: 35%;
  text-align: left;
}

.section {
  display: inline-block;
  text-align: left;
  width: 45%;
  margin-bottom: 25px;
}

.section-correct {
  display: block;
}

.section-correct h6 {
  margin: 0;
  margin-top: 15px;
  padding: 15px 0;
  font-size: 0.9rem;
  font-weight: 400;
  color: #07E0E0;
}

.section-time {
  display: inline;
  float: left;
  margin-left: 80px;
}

.section-time h6 {
  margin: 0;
  margin-left: 0px;
  margin-top: 15px;
  padding: 15px 0;
  font-size: 0.9rem;
  font-weight: 400;
  color: #07E0E0;

}


.section h6 {
  margin: 0;
  margin-top: 30px;
  padding: 15px 0;
  font-size: 0.9rem;
  font-weight: 400;
  color: #07E0E0;
}

.section:nth-of-type(2) {
  float: right;
}

select {
  border: 0;
}

.answer-input-container {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 5px;
  margin-bottom: 5px;
}

.modal-input, .modal-answer-input {
  display: flex;
  flex-direction: column;
  margin-top: 5px;
  margin-bottom: 5px;
}

.modal-input {
    width: 80%;
}

label {
  margin: 0;
  padding: 0;
  text-align: left;
  font-size: 1rem;
  font-weight: 400;
  color: #07E0E0;
}

.modal-input h6 {
  padding-top: 20px;
  margin: 0;
  text-align: left;
  font-size: 1rem;
  font-weight: 400;
  color: #07E0E0;
}

.modal-answer-input {
    width: 90%;
}

.radio {
  display: flex;
  align-items: center;
}

.edit-question, .edit-answer {
    margin: 0;
    padding: 10px 0;
    border: 0;
    height: 1.5em;
    font-family: sans-serif;
    font-size: 1em;
    color: #333;
    background: inherit;
}

.edit-question:hover, .edit-answer:hover {
  border-bottom: 2px solid #07E0E0;
}

.edit-answer::placeholder {
    font-family: sans-serif;
    font-size: 16px; //color: rgba(46, 213, 116, 0.839);
    color: rgba(15, 15, 15, 0.4);
}

.edit-question, .edit-answer, .correct-answer:focus {
  outline: none;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.modal-btn {
  display: flex;
  justify-content: center;
  padding: 0.5rem 1rem;
  margin: 0 0.8rem;
  /* background-color: #417cef; */
  border: 0;
  font-size: 18px;
  border-radius: 3px;
  text-decoration: none;
  font-family: sans-serif;
  background-color: rgba(15, 15, 15, 0.03);
  margin-top: 20px;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}

.modal-btn:hover {
    transform: scale(0.97);
    outline: none;
}

.modal-btn:focus {
  outline: none;
}

.modal-btn:active {
    transform: scale(0.94);
}

.save {
  color: #fff;
  background: #07E0E0;
}

.cancel {
  border: 1px solid #07E0E0;
  background: #fff;
}

.delete {
  color: #f00;
  border: 1px solid #f00;
  background: #fee;
}

.danger-error {
    color: #e74c3c;
    font-size: 16px;
}

.radio input[type="radio"] {
  display: inline;
}

.question-label-main {
  font-weight: 500 !important;
font-size: 26px;
}

.question-label {
  font-weight: bold !important;
font-size: 14px !important;
}

.question-editGame {
  font-weight: 300;
font-size: 20px;
line-height: 23px;
display: flex;
}

.answerchoice-editGame {
font-size: 20px;
line-height: 23px;
}

.back {
margin-left: 80px;
}


.addQButton {
  padding-top: 10px;
  float: left;
  margin-left: 40px;
  background-color: none !important;
}

.addQImg {
  width: 1800px;
  height: 30px;
  background-color: none !important;
}
